.App {
  text-align: center;
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: black;
}

.window {
  display: flex;
  flex-direction: column;
  justify-content: stretch;
  align-items: stretch;
  color: inherit;
  border: solid 3px white;
  margin: 3vh 0px;
  font-size: calc(10px + 2vmin);
  width: 100vh;
  height: 60vh;
}

.header {
  color: white;
  height: 10vh;
  font-size: calc(20px + 2vmin);
  background-color: black;
}

#split-screen {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: row;
  background-color: white;
}

#left-side {
  height: 100%;
  width: 50%;
}

#right-side {
  width: 50%;
}
.key-grid {
  display: grid;
  padding-top: 1.5vh;
  justify-content: center;
  align-content: center;
  grid-template-columns: calc(45vh/3) calc(45vh/3) calc(45vh/3);
  grid-template-rows: calc(45vh/3) calc(45vh/3) calc(45vh/3);
  grid-gap: calc(5vh/3);
}
#display {
  text-align: center;
  font-size: 5vh;
  padding-top: 45%;
}

.drum-pad {
  color: black;
  background-color: white;
  font-size: calc(10px + 2vmin);
  border: solid 1px black;
}
.drum-pad:hover {
  background-color: black;
  font-size: calc(20px + 2vmin);
  color: white;
}